<template>
  <div class="c">
    <div class="prevent-page">
    <h1>每日心理健康检查</h1>

    <!-- 心理测评部分 -->
    <div class="assessment" v-if="!assessmentCompleted">
      <h2>简单心理测评</h2>
      <p>请回答以下问题,选择最符合你当前状态的选项:</p>
      <div v-for="(question, index) in questions" :key="index" class="question">
        <p>{{ question.text }}</p>
        <select v-model="answers[index]">
          <option value="">请选择</option>
          <option value="1">很少</option>
          <option value="2">有时</option>
          <option value="3">经常</option>
          <option value="4">总是</option>
        </select>
      </div>
      <button @click="submitAssessment" :disabled="!allQuestionsAnswered">
        提交测评
      </button>
    </div>

    <!-- 测评结果和每日心情记录 -->
    <div v-else class="mood-tracker">
      <h2>测评结果</h2>
      <p>你的心理健康评分: {{ assessmentScore }} / 20</p>
      <p>{{ assessmentFeedback }}</p>
      <hr>
      <h2>今日心情</h2>
      <select v-model="dailyMood">
        <option value="">请选择你的心情</option>
        <option value="很开心">很开心 😄</option>
        <option value="还不错">还不错 🙂</option>
        <option value="一般">一般 😐</option>
        <option value="有点低落">有点低落 😔</option>
        <option value="非常沮丧">非常沮丧 😢</option>
      </select>
      <button @click="saveDailyMood" :disabled="!dailyMood">保存今日心情</button>

      <div v-if="moodHistory.length > 0">
        <h3>最近的心情记录</h3>
        <ul>
          <li v-for="(mood, index) in moodHistory.slice(0, 5)" :key="index">
            {{ mood.date }}: {{ mood.mood }}
          </li>
        </ul>
      </div>
    </div>
  </div>
  </div>
  
</template>

<script>
export default {
  name: 'PreventPage',
  data() {
    return {
      questions: [
        { text: "1. 你感到压力很大吗?" },
        { text: "2. 你容易感到焦虑或紧张吗?" },
        { text: "3. 你经常感到沮丧或心情低落吗?" },
        { text: "4. 你对日常活动失去兴趣了吗?" },
        { text: "5. 你睡眠质量不好?" }
      ],
      answers: [],
      assessmentCompleted: false,
      assessmentScore: 0,
      dailyMood: '',
      moodHistory: []
    }
  },
  computed: {
    allQuestionsAnswered() {
      return this.answers.length === this.questions.length && 
             this.answers.every(answer => answer !== '')
    },
    assessmentFeedback() {
      if (this.assessmentScore <= 5) {
        return "你的心理健康状况看起来不错!继续保持积极的生活方式。"
      } else if (this.assessmentScore <= 10) {
        return "你可能正经历一些压力。试着多休息,和朋友聊聊天。"
      } else if (this.assessmentScore <= 15) {
        return "你似乎正面临一些挑战。考虑寻求专业帮助或与信任的人交流。"
      } else {
        return "你可能正处于较大的心理压力中。强烈建议你寻求专业的心理健康支持。"
      }
    }
  },
  methods: {
    submitAssessment() {
      this.assessmentScore = this.answers.reduce((sum, value) => sum + parseInt(value), 0)
      this.assessmentCompleted = true
      this.saveAssessmentResult()
    },
    saveDailyMood() {
      const today = new Date().toLocaleDateString()
      this.moodHistory.unshift({ date: today, mood: this.dailyMood })
      this.saveMoodHistory()
      this.dailyMood = ''
    },
    saveAssessmentResult() {
      localStorage.setItem('assessmentResult', JSON.stringify({
        score: this.assessmentScore,
        date: new Date().toLocaleDateString()
      }))
    },
    saveMoodHistory() {
      localStorage.setItem('moodHistory', JSON.stringify(this.moodHistory))
    },
    loadSavedData() {
      const savedAssessment = localStorage.getItem('assessmentResult')
      if (savedAssessment) {
        const { score, date } = JSON.parse(savedAssessment)
        if (date === new Date().toLocaleDateString()) {
          this.assessmentScore = score
          this.assessmentCompleted = true
        }
      }

      const savedMoodHistory = localStorage.getItem('moodHistory')
      if (savedMoodHistory) {
        this.moodHistory = JSON.parse(savedMoodHistory)
      }
    }
  },
  mounted() {
    this.loadSavedData()
  }
}
</script>

<style scoped>
.c{
  height: 800px;
}
.prevent-page {
  max-width: 800px;
  
  margin: 0 auto;
  padding: 10px;
  font-family: Arial, sans-serif;
 
}

h1, h2, h3 {
  color: #333;
}

.question {
  margin-bottom: 15px;
}

select, button {
  margin-top: 10px;
  padding: 5px 10px;
  font-size: 16px;
}

button {
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

button:disabled {
  background-color: #cccccc;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 5px;
}
</style>