//引入VueRouter
import VueRouter from 'vue-router'
//引入Luyou 组件
import InterferePage from '../pages/InterferePage.vue'
import PreventPage from '../pages/PreventPage.vue'
import PsyHome from '../pages/PsyHome.vue'
import PsyWeb from '../pages/PsyWeb.vue'

//创建router实例对象，管理一组一组的路由规则
const router = new VueRouter({
    routes:[
        {
            path:'/',
            component:PsyWeb
        },
        {
            path:'/PsyHome',
            component:PsyHome
        },
        {
            path:'/Prevent',
            component:PreventPage
        },
        {
          path:'/Interfere',
          component:InterferePage
        }
    ]
})

//暴露router
export default router